import {Doughnut, mixins} from 'vue-chartjs'

const {reactiveProp} = mixins;
export default {
    extends: Doughnut,
    mixins: [reactiveProp],
    name: "VueChart",
    props: ['options'],
    watch: {
        chartData () {
            this.$data._chart.update()
        }
    },
    mounted() {
        this.renderChart(this.chartData, this.options)
    }
}

