<template>
    <div class="item-selection">
        <v-select
                v-if="form_parameters.filter_box_layout==='select'"
                v-model="currentValue"
                :options="getSelectOptions"
                :label="form_parameters.label_field"
                @input="selectionChanged"

        >
        </v-select>
        <div class="filter-parameters" v-if="form_parameters.filter_box_layout==='tabs'">
            <div class="filter-parameter"
                 v-for="param in form_parameters.filter_parameters"
                 @click="tabSelectionChanged(param)"
                 :class="{'active':param.select_by === currentValue.select_by}"
            >
                {{param.title}}
            </div>
        </div>

        <div class="nested-component">
            <component
                    :is="form_parameters.nested_component_type"
                    v-if="!loading_nested_component"
                    :items="getCurrentValue"
                    :fields="getFields"
                    :models="{Item:[currentValue]}"
                    :form_parameters="form_parameters"
                    class="full-width"
            >
            </component>
            <div v-else class="spinner-container">
                <atom-spinner
                        :animation-duration="1000"
                        v-show="true"
                        color="var(--accent)"
                />
            </div>
        </div>
    </div>
</template>

<script>
    import Zone from "../Zone";
    import VSelect from 'vue-select';
    import VueChart from '../vue-charts/VueChart'
    import PanelList from './PanelList'
    import {AtomSpinner} from 'epic-spinners'
    import Velocity from 'velocity-animate'
    export default {
        name: "ItemSelection",

        data() {
            return {
                currentValue: null,
                loading_nested_component: false
            }
        },
        props: ['items', 'fields', 'form_parameters'],

        computed: {
            /**
             * Определение наличия вложенного списка
             * @returns {*|boolean}
             */
            nested_list() {
                return this.form_parameters.nested_component_type
                    && this.form_parameters.nested_component_type === 'PanelList'
            },

            /**
             * Определение значений для дочернего компонента в зависимости от значения в селекте
             * @returns {null|*}
             */
            getCurrentValue() {
                if (this.nested_list) {
                    return this.items.filter(item => {
                        return item.select_name === this.currentValue.select_by
                    })
                }
                return this.currentValue;

            },

            /**
             * Определения перечня и формата полей для дочернего компонента в зависимости от его типа
             * @returns {string|*}
             */
            getFields() {
                if (this.nested_list) return this.fields;
                return this.fields.map(field => {
                    return {
                        model: field.key,
                        title: field.label,
                        model_name: 'Item',
                        width: '50%',
                        type: 'disabledInput'
                    }
                })
            },

            getSelectOptions() {
                if (this.nested_list) {
                    return this.form_parameters.filter_parameters
                }
                return this.items
            }
        },

        methods: {
            selectionChanged(e) {
                if (e) {
                    this.loading_nested_component = true;
                    setTimeout(() => {
                        this.loading_nested_component = false;
                    }, 500)
                }
            },
            tabSelectionChanged(filter_parameter) {
                this.currentValue = filter_parameter
            }
        },

        created() {

            this.nested_list ? this.currentValue = this.form_parameters.filter_parameters[0] : this.currentValue = this.items[0];
        },


        components: {
            VSelect,
            Zone,
            VueChart,
            AtomSpinner,
            PanelList
        }

    }
</script>

<style scoped lang="scss">
    @import "../../assets/sass/variables";

    .v-select {
        padding: 0 15px;
        margin-bottom: 1rem;
    }

    .filter-parameters {
        display: flex;
        padding: 0 15px 10px 15px;

        .filter-parameter {
            margin-right: 2rem;
            font-size: 1.4rem;
            font-weight: 600;
            cursor: pointer;
            &:first-of-type {
                margin-left: 2rem;
            }

            &.active {
                color: $accent;
                text-decoration: underline;
            }
        }
    }

    .spinner-container {
        display: flex;
        justify-content: center;
        padding: 2rem;
    }
</style>