<template>
    <div class="home-panel">
<!--        <h1>Домашняя страница</h1>-->

        <div class="home-panel-layout">
            <div class="home-panel-left-side">


                <PanelItem
                           v-for="item in panel_items.filter(panel_item=>panel_item.form_parameters.column===1)"
                           :item="item"
                >

                </PanelItem>
            </div>
            <div class="home-panel-right-side">
                <PanelItem
                        v-for="item in panel_items.filter(panel_item=>panel_item.form_parameters.column===2)"
                        :item="item"
                >
                </PanelItem>
            </div>
        </div>
    </div>
</template>

<script>
    import PanelItem from './PanelItem'

    export default {
        name: "HomePanel",
        data() {
            return {
                panel_items: [],
                fields: []
            }
        },
        methods: {
            async fetchListData(route, params = {}) {
                try {
                    let res = await this.$http.post(route, params);
                    this.panel_items = res.data.panel_items;

                } catch (e) {
                    console.log(e);
                }
            },

        },
        created() {
            this.fetchListData('/admin/get/home/panel/elements');
        },
        components: {
            PanelItem
        }
    }
</script>

<style lang="scss">

    .home-panel {

        &-layout {
            display: flex;
        }

        &-left-side, &-right-side {
            width: 50%;


        }
    }
</style>